
.rtl {
    direction: rtl;
}
@font-face {
    font-family: 'IRANSansWebs';
    src:  url('IRANSansWeb.ttf');
}
*{
    font-family: IRANSansWebs!important;
}
body{
    background: #FFF3F6;
}
Link{
    text-decoration: none;

}
a{
    text-decoration: none!important;
}
.baner-top img {
    height: 280px;
}

.baner-top .title {
    background: rgba(0, 0, 0, 0.6);;
    width: 100%;
    position: absolute;
    right: 0;
    padding-right: 20px;
    text-align: right;
    margin-top: 200px;
    color: white;
    border-radius: 0 0 20px 20px;

}

.round {
    border-radius: 20px!important;

}
.w-33{
    width: 33%;
}
.card-footer{
    border-bottom: none;
    background: white;
}
.route-wrapper {
    position: relative;
}

.route-wrapper > div {
    position: absolute;
}
.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    color: #fff;
    background-color: #FF6F91;
    border-radius: 20px!important;

}.nav-pills .nav-link {
    color: #FF6F91;
}
 .bg-danger{
     background-color: #FF6F91!important;
     color: #fff;
     font-weight: bolder;
     border-radius: 20px!important;
     margin-top: 5px;
 }
 .cardd{
     border-radius: 20px!important;
transition: 0.5s;
     border: none;
     -webkit-box-shadow: 0px 3px 6px -3px rgba(255,111,145,0.58);
     -moz-box-shadow: 0px 3px 6px -3px rgba(255,111,145,0.58);
     box-shadow: 0px 3px 6px -3px rgba(255,111,145,0.58);

 } .cardd:hover{
     border: none;
        border-radius: 20px!important;

        -webkit-box-shadow: 0px 8px 14px 2px rgba(255,111,145,0.58);
        -moz-box-shadow: 0px 8px 14px 2px rgba(255,111,145,0.58);
        box-shadow: 0px 8px 14px 2px rgba(255,111,145,0.58);
 }
   .card-header{
       border: none!important;
       border-bottom: none!important;
   }
   .bgappbar{
       background: #FF6F91;
       box-shadow: none;
   }